
import userModel from "@/model/user"
import CuiPicker from "@/components/cui-vue/cui-picker/CuiPicker"
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: "UserInfo",
data()
{
return {
birth:null,
username:"",
sex:"boy",
headimgActionSheet:false,
headimgSettingMenu:{
file: '文件',
photo: '图片',
carma: '摄像'
}
}
},
components:{
Actionsheet,
CuiPicker
},
beforeRouteLeave(to,from,next)
{
to.meta.keepAlive=true;
next();
},
methods:
{
selectFileType:function(key)
{
console.log(key)
},

selectSex:function(sex)
{
this.sex=sex
},

showSelectPicture:function(){
this.headimgActionSheet=true
},

setBirth:function()
{
let that=this,
date=new Date()
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
maxYear:date.getFullYear(),
minYear:"1959",
format:"YYYY-MM",
value: that.birth, // 其他参数同 props
onHide () {
const _this = this
that.birth=_this.value
},
onShow () {
const _this = this
}
})

}
},
created()
{
console.log("userinfo")
}
}



